import React from "react";

function App() {

let store = {
  butt: [
    {val: "C", id: "C"},{val: "←"},{val: "+"}
  ],
  buttons: [
    {val: "7"},{val: "8"},{val: "9"},{val: "÷"},{val: "4"},{val: "5"},{val: "6"},{val: "×"},{val: "1"},{val: "2"},{val: "3"},{val: "-"},{val: "0", id: "zero"},{val: ","},{val: "="}
  ]
}

const [count, setCount] = React.useState("0");
const [bool, setBool] = React.useState(true);

function Reset (event) {
  switch (event) {
    case "←":
      if (count.length === 1) {
        setCount("0");
        if (!bool) {
          setBool(!bool);
        }
      } else {
        setCount(count.slice(0, -1));
      }
      break;

    case "C":
      setCount("0");
      if (!bool) {
        setBool(!bool);
      }
    break;

    case "=":
      try {
        if (bool === true) {
          setCount("Error");
        }else {
          let result = Math.round(eval(eval(count.replaceAll(",", ".").replaceAll("×", "*").replaceAll("÷", "/"))) * Math.pow(10, 5)) / Math.pow(10, 5);
          if (result === Infinity || result === -Infinity || Number.isNaN(result)) {
            setCount("Error");
            if (!bool) {
              setBool(!bool);
            }
          }else {
            setCount(result);
            console.log(result);
          }
        }
      } catch (error) {
        setCount("Error");
        if (!bool) {
          setBool(!bool);
        }
      }



    break;

    default:
      if (bool === true) {
        setCount("");
        setCount(event);
        setBool(!bool);
      } else {
        setCount(count + event);
      }
    break;
  }
}



  return (
    <div className="container">
      <div className="calculator">
        <div className="output">
          <input type="text" value={count} defaultValue={0}/>
        </div>
        <div className="buttons">
          {store.butt.map(item => <button onClick={() => Reset(item.val)} className={item.id}>{item.val}</button>)}
        </div>
        <div className="buttons_2">
          {store.buttons.map(item => <button onClick={() => Reset(item.val)} className={item.id}>{item.val}</button>)}
        </div>
      </div>
    </div>
  );
}

export default App;
